import React from 'react'
import SwiperCore, { Autoplay,Pagination } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
SwiperCore.use([Autoplay,Pagination]);

export default () => {
  return (
<div className='swiper review slider-ct'>
<p className="title title-ct">Clients Opinion</p>
<Swiper  pagination={
  {clickable:true}
}
      spaceBetween={30} 
      loop={true}
      autoplay={{
        delay:7000,
        disableOnInteraction:false
    }}
    >
      <SwiperSlide>
<div className='client-review'>
    <p className='client-review-des'>IBTSBD has developed our Cloud Data Warehouse, all the data pipelines and BI architecture from grounds up, highly recommended them</p>
    <p className='client-name'>– Yannick Will , Lindera Gmbh
</p>
</div>
      </SwiperSlide>
      <SwiperSlide>
<div className='client-review'>
    <p className='client-review-des'>We have worked with IBTSBD in many different projects, they handled all the work professionally</p>
    <p className='client-name'>– T. Hempel , Bookingkit Gmbh</p>
</div>
      </SwiperSlide>
      <SwiperSlide>
<div className='client-review'>
    <p className='client-review-des'>Our data migration work from on-premise NoSQL database to Amazon Redshift data warehouse managed by IBTSBD, they also created and currently managing our Business intelligence reporting setup, we happy with their effort</p>
    <p className='client-name'>-Ben Jones , Bitwala Gmbh</p>
</div>
      </SwiperSlide>
    </Swiper>
</div>
  );
};